import React from 'react';
import "./PushList.css";

const PushList = ({addItem, items}) => {
    const input = React.useRef();
    const onSubmitToDo = (e) => {
        if (e.keyCode === 13 && input.current.value) {
            addItem([...items, e.target.value]);
            localStorage.setItem('items',[...items,e.target.value]);
            input.current.value = [];
            console.log(items);
        }
    }
    const onAddList = () => {
        if (input.current.value){
            addItem([...items, input.current.value]);
            localStorage.setItem('items',[...items,input.current.value]);
            input.current.value = [];
            console.log(items);
        }
    }
    const onDelList = () => {
        input.current.value = [];
        console.log(items);
    }
    return (<div>
            <input ref={input} onKeyDown={(e) => onSubmitToDo(e)} className="push-list" type="text" name="list"
                   placeholder="Добавить дело"></input>
            <div className="manage-btn">
                <button onClick={() => onAddList()} className="btn">Добавить</button>
                <button onClick={() => onDelList()} className="btn">Очистить</button>
            </div>
        </div>
    );
};

export default PushList;