import React from 'react';
import "./ListBlock.css";
import trash from '../../assets/trash.svg';
import dropedTrash from '../../assets/dropedTrash.svg';
import unchecked from '../../assets/unchecked.svg';
import checked from '../../assets/checked.svg';

const ListBlock = ({items, addItem}) => {
    const [checkbox, setCheckbox] = React.useState(false);
    const [bucket, setBucket] = React.useState(false);
    const delElem = (arr, index) => {
      arr.splice(index);
        return arr;
    }
    const onToggleCheckbox = () => {
        setCheckbox(checkbox => !checkbox);
    }
    const onToggleTrash = () => {
        setBucket(bucket => !bucket);
    }
    const onRemoveList = (index) => {
        const deleteList = window.confirm('ВЫ действительно хотите удалить этот лист');
        if (deleteList) {
            addItem(items=> delElem(items,index));
            console.log(items);
        }
    }
    return (
        <div className="list-block">
            {
                items && items.map((e, index) =>
                    <div className="list__item" key={`${e}_${index}`}>
                        <img onClick={() => onToggleCheckbox()} className='checkbox'
                             src={checkbox ? checked : unchecked} alt="checkbox"/>
                        <p>{e}</p>
                        <img className='trash' onClick={() => onRemoveList(index)} onMouseOver={() => onToggleTrash()}
                             onMouseOut={() => onToggleTrash()} src={bucket ? dropedTrash : trash} alt="ведро"/>
                    </div>
                )
            }
        </div>
    );
};

export default ListBlock;